.content {
  position: absolute;
  padding: 0 18px;
  left: 0;
  top: 80px;
  background-color: #EDF9FE;
  margin-top: 0;
  transition: margin-top 0.6s ease;

  @media (min-width: 1024px) {
    top: 100px;
    padding: 0 100px;
  }

  &--topMargin {
    margin-top: 280px;
    transition: margin-top 0.6s ease;
  }
}

.home {

  &__content {
    display: flex;
    flex-direction: column;

    @media (min-width: 1024px) {
      flex-direction: row;
    }
  }

  &__textContent {
    @media (min-width: 1024px) {
      flex: 1;
    }
  }

  &__title {
    margin: 30px auto 40px;
    text-align: center;
    color: #24385D;
    font-size: 1.5em;
    font-weight: 600;

    @media (min-width: 1024px) {
      font-size: 3.5em;
      text-align: start;
    }
  }

  &__text {
    font-size: 0.875em;
    font-weight: 500;
    color: #8795B0;
    text-align: center;
    margin-bottom: 30px;

    @media (min-width: 1024px) {
      font-size: 2em;
      text-align: start;
      margin-bottom: 40px;
    }
  }

  &__social {
    display: flex;
    justify-content: center;
    gap: 10px;

    @media (min-width: 1024px) {
      justify-content: start;
    }
  }

  &__icon {
    width: 30px;
    height: 30px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @media (min-width: 1024px) {
      width: 40px;
      height: 40px;
    }

    &--telegram {
      background-image: url(../../../images/telegram.svg);
    }

    &--whatsapp {
      background-image: url(../../../images/whatsapp.svg);
    }

    &--facebook {
      background-image: url(../../../images/facebook.svg);
    }

    &--viber {
      background-image: url(../../../images/viber.svg);
    }

    &--gmail {
      background-image: url(../../../images/gmail.svg);
    }

    &--phone {
      background-image: url(../../../images/phone.svg);
    }
  }

  &__img {
    @media (min-width: 1024px) {
      flex: 1;
    }
  }
}